class FormStore {

    constructor() {
        this.store = {}
    }
    
    addStore(name,data) {
        this.store[name] = data
    }

    releaseStore (name){
        this.store[name] = {}
    }

    changeStoreData (name,key,value){
        console.log('输出key', this.store[name][key])
        // this.store[name][key]['dataSource'] = value
    }
}


export default new FormStore